  border-right-color: var(--coach-white);
}

.irs--flat .irs-line {
  background-color: var(--coach-gray-200);
  height: 3px;
  top: 30px;
}
.irs--flat .irs-bar {
  height: 3px;
  background-color: var(--coach-primary);
  top: 30px;
}
.irs--flat .irs-handle > i:first-child {
  position: absolute;
  display: block;
  top: 0;
  left: 50%;
  width: 18px;
  height: 100%;
  margin-left: -1px;
  background-color: var(--coach-white);
  border-radius: 100%;
  box-shadow: 0px 1px 3px rgba(0, 0, 0, 0.1);
}
.irs--flat .irs-handle.state_hover > i:first-child,
.irs--flat .irs-handle:hover > i:first-child {
  background-color: var(--coach-primary);
}
.irs--flat .irs-from,
.irs--flat .irs-to,
.irs--flat .irs-single {
  color: var(--coach-dark);
  font-size: 13px;
  line-height: 1.333;
  text-shadow: none;
  padding: 5px 9px;
  background-color: transparent;
  border: 1px solid var(--coach-gray-200);
  border-radius: 4px;
  margin-left: 7px;
}
.irs--flat .irs-from:before,
.irs--flat .irs-to:before,
.irs--flat .irs-single:before {
  display: none;
}
.irs--flat .irs-min,
.irs--flat .irs-max {
  display: none;
}
.irs--flat .irs-grid-pol {
  position: absolute;
  top: 0;
  left: 0;
  width: 1px;
  height: 18px;
  background: var(--coach-gray-200);
}
.irs--flat .irs-grid-pol.small {
  height: 9px;
}
.irs--flat .irs-grid-text {
  position: absolute;
  bottom: -7px;
  left: 0;
  white-space: nowrap;
  text-align: center;
  font-size: 12px;
  line-height: 14px;
  padding: 0px 3px;
  color: var(--coach-gray-600);
}

.irs-from,
.irs-to,
.irs-single {
  position: absolute;
  display: block;
  top: -12px;
  left: 0;
  cursor: default;
  white-space: nowrap;
}

/* Inputmask */
@keyframes blink {
  from, to {
    border-right-color: var(--coach-gray-900);
  }
  50% {
    border-right-color: transparent;
  }
}
mark.im-caret, .im-caret.mark {
  animation: 1s blink step-end infinite !important;
}

mark.im-caret-select, .im-caret-select.mark {
  background-color: rgba(0, 0, 0, 0.25);
}

span.im-static {
  color: var(--coach-gray-400);
}

div.im-colormask {
  display: inline-block;
  border-style: inset;
  border-width: 2px;
  appearance: textfield;
  cursor: text;
}
div.im-colormask > input {
  position: absolute !important;
  display: inline-block;
  background-color: transparent;
  color: transparent;
  -webkit-text-fill-color: transparent;
  transition: background-color 5000s ease-in-out 0s;
  caret-color: transparent;
  text-shadow: none;
  appearance: caret;
  border-style: none;
  left: 0;
}
div.im-colormask > input:-webkit-autofill {
  position: absolute !important;
  display: inline-block;
  background-color: transparent;
  color: transparent;
  -webkit-text-fill-color: transparent;
  transition: background-color 5000s ease-in-out 0s;
  caret-color: transparent;
  text-shadow: none;
  appearance: caret;
  border-style: none;
  left: 0;
}
div.im-colormask > input:-webkit-autofill ~ div {
  background-color: rgb(250, 255, 189);
}
div.im-colormask > input:focus {
  outline: none;
}
div.im-colormask > input::selection {
  background: none;
}
div.im-colormask > input::-moz-selection {
  background: none;
}
div.im-colormask > div {
  color: #261f44;
  display: inline-block;
  width: 100px;
}

[im-insert=false] {
  caret-color: var(--coach-danger);
  caret-shape: block;
}

.plyr {
  border-radius: 0.5rem;
  height: 250px;
}
.plyr:hover.plyr--video .plyr__controls {
  display: flex;
}

.plyr__poster {
  background-size: cover;
}

.plyr--video .plyr__controls {
  display: none;
}

.plyr--full-ui.plyr--video .plyr__control--overlaid {
  display: none;
}